* {
	box-sizing: border-box;
	margin: 0;
	outline: none;
	font-family: 'Futura PT';
}


.error {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    font-weight: bold;
    font-family: 'Futura PT';
    font-size: 30px;
}
.error div {
    color: red;
    width: 70%;
}
.light {
    font-weight: lighter;
    letter-spacing: 0.5px;
    font-size: 14px;
}
.medium {
    font-weight: normal;
    letter-spacing: 0.5px;
    font-size: 14px;
}
.bold {
    font-weight: bold;
    letter-spacing: 1px;
    text-transform: uppercase;
    font-size: 16px;
}
.header_container {
	margin-top: 30px;
	width: 100%;
	height: 70px;
	background-color: rgb(0, 0, 0);
	color: white;
	font-size: 20px;
	display: flex;
	align-items: center;
    justify-content: center;
}
.logo_wrapper {
	flex-grow: 3;
    display: flex;
    justify-content: flex-end;
}
.logo {
	/* font-family: FuturaPT-Bold; */
    text-transform: uppercase;
    font-size: 42px;
    cursor: pointer;
    margin-left: 40px;
    color: white;
    font-weight: bold;
}
.product_wrapper {
    
    border-left: 1px solid #525050;
    border-right: 1px solid #525050;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-grow: 0.3;
    margin: 0 20px;
    transition: 0.3s background-color ease;
    font-weight: bold;
    letter-spacing: 1.5px;
    text-transform: uppercase;
    font-size: 16px;
}
.product_wrapper.active {
    background-color: #525050;
    cursor: default;
}
.product_list_container {
    width: 100%;
    height: 60px;
    background-color: #525050;
    display: flex;
    justify-content: space-evenly;
}
.product_element_wrapper {
    width: 70%;
    display: flex;
}
.product_element {
    color: white;
    cursor: default;
    max-height: 100%;
    padding: 0 10px;
    display: flex;
    align-items: center;
    font-weight: normal;
    letter-spacing: 1px;
    font-size: 16px;
    object-fit: cover;
}
.product_element_wrapper button {
    color: #fff;
    letter-spacing: .8px;
    line-height: 60px;
    border: none;
    padding: 0 10px;
    background-color: #525050;
    margin: 0;
    outline: none;
    border-radius: 0;
}
.product_element.sale {
    display: block;
    padding: 0 12px 0 14px;
    background-color: #d01345;
    transform: skew(-12deg);
    padding: 0 10px;
    font-weight: bold;
    letter-spacing: 1px;
    font-size: 16px;
}
.product_element.sale:hover {
    background-color: white;
    transform: skew(0deg);
}
.product_element:hover {
    background-color: white;
    color: #525050;
}

.svg_wrapper {
