    border: .5px solid #777;
}
.search_wrapper {
    width: 850px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    background-color: rgba(255, 255, 255, 0);
    z-index: 20;
}
.search_input {
	width: 100%;
	height: 40px;
	border: 0;
    font-size: 18px;
    font-weight: normal;
    letter-spacing: 0.5px;
    padding-left: 16px;
    padding-right: 75px;
    border-radius: 21px/50%;
    z-index: 20;
}
.search_input::-webkit-input-placeholder {
    font-weight: normal;
    letter-spacing: 0.5px;
    font-size: 16px;
}
.search_input:-ms-input-placeholder {
    font-weight: normal;
    letter-spacing: 0.5px;
    font-size: 16px;
}
.search_input::-ms-input-placeholder {
    font-weight: normal;
    letter-spacing: 0.5px;
    font-size: 16px;
}
.search_input::placeholder {
    font-weight: normal;
    letter-spacing: 0.5px;
    font-size: 16px;
}
.svg_search_wrapper {
    position: absolute;
    right: 0px;
    background-color: white;
    width: 40px;
    height: 40px;
    border-radius: 20px;
    z-index: 20;
    transition: all .3s ease;
    cursor: pointer;
}
.svg_search_wrapper.active {
    background-color: rgb(0, 197, 223);
}
.svg_search_wrapper:active {
    border-style: inset;
}
.svg_search {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-45%, -50%);
    z-index: 20;
    fill: black;
    transition: all .3s ease;
}
.svg_search.active {
    fill: white;
}
.clear_search {
    position: absolute;
    top: 50%;
    right: 43.5px;
    width: 15px;
    height: 15px;
    z-index: 20;
    transform: translateY(-50%);
    cursor: pointer;
    pointer-events: none;
}
.clear_search:before, .clear_search:after {
    position: absolute;
    content: ' ';
    height: 16px;
    width: 2px;
    background-color: rgb(0, 197, 223);
    opacity: 0;
    border-radius: 20px;
    transition: all .3s ease;
}
.clear_search.active:before, .clear_search.active:after {
    opacity: 1;
    pointer-events: all;
}
.clear_search:before {
    transform: rotate(45deg);
}
.clear_search:after {
    transform: rotate(-45deg);
}
.search_bcg {
    background-color: rgb(0, 0, 0, 0.550);
    position: fixed;
    top: -40px;
    left: 0;
    right: 0;
    width: 100%;
    height: calc(100vh + 40px);
    opacity: 0;
    transition: opacity .3s ease-in;
    pointer-events: none;
    z-index: 10;
}
.bcg_hide {
    opacity: 1;
    pointer-events: all;
}

@-webkit-keyframes slide {
    0% {
        opacity: 0;
        transform: scale(1.1);
    }
    100% {
        opacity: 1;
        transform: scale(1);
    }
}

@keyframes slide {
    0% {
        opacity: 0;
        transform: scale(1.1);
    }
    100% {
        opacity: 1;
        transform: scale(1);
    }
}

@-webkit-keyframes text {
    0% {
        opacity:  0;
        transform:  translateX(-100px);
    }
    100% {
        opacity:  1;
        transform:  translateX(0px);
    }
}

@keyframes text {
    0% {
        opacity:  0;
        transform:  translateX(-100px);
    }
    100% {
        opacity:  1;
        transform:  translateX(0px);
    }
}

.home {
    /* height: 600px; */
    width: 70%;
    margin: 0 auto;
    margin-top: 60px;
    background-color: black;
    overflow: hidden;
    position: relative;
}
.slider {
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
}
.slide {
    background-repeat: no-repeat;
    width: 80%;
    height: 100%;
    background-size: cover;
    background-position: center;
    display: none;
    -webkit-animation: slide 2s ease;
            animation: slide 2s ease;
    padding: 25% 0;
    position: relative;
}
.slide.active {
    display: block;
}
.caption {
    position: relative;
    width: 130px;
    height: 40px;
    transform-style: preserve-3d;
    position: absolute;
    top: 20%;
    font-weight: bold;
    letter-spacing: 1px;
    text-transform: uppercase;
    font-size: 16px;
}
.caption .face {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transform-style: preserve-3d;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 2s ease-in-out;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    transform: perspective(500px) rotateY(0deg);
    cursor: pointer;
}
.caption .face.front {
    background-color: rgba(0, 0, 0, 0);
    
}
.caption:hover .face.front {
    transform: perspective(500px) rotateY(180deg);
}
.caption .face.back {
    background-color: rgba(0, 0, 0, 0);
    transform: perspective(500px) rotateY(180deg);
    display: flex;
    justify-content: center;
    align-items: center;
}
.caption .face.back h1 {
    color: white;
}
.back {
    border: 0.5px solid rgb(255, 255, 255);
}
.back:before {
    content: '';
    position: absolute;
    top: 5px;
    left: -4px;
    width: calc(100% + 8px);
    height: calc(100% - 12px);
    background-color: black;
    transition: .8s ease-in-out; 
    transform: scaleY(1);
}
.back:hover:before {
    transform: scaleY(0);
}
.back:after {
    content: '';
    position: absolute;
    left: 5px;
    top: -4px;
    height: calc(100% + 8px);
    width: calc(100% - 12px);
    background-color: black;
    transition: .8s ease-in-out; 
    transform: scaleX(1);
}
.back:hover:after {
    transform: scaleX(0);
}

.back h1 {
    position: relative;
    z-index: 3;
}
.caption:hover .face.back {
    transform: perspective(500px) rotateY(360deg);
}
.caption .face h1 {
    text-transform: uppercase;
    /* transform: perspective(500px) translateZ(90px); */
    font-size: 16px;
}
.slide.active .caption  {
    opacity: 0;
    -webkit-animation: text 1s ease forwards;
            animation: text 1s ease forwards;
    -webkit-animation-delay: 1s;
            animation-delay: 1s;
}
.slide.active .caption button  {
    border: 0;
    background-color: rgba(255, 255, 255, 0);
    width: auto;
    height: auto;
    line-height: inherit;
    margin-top: 5px;
    color: white;
    text-transform: uppercase;
    font-size: 20px;
    cursor: pointer;
    font-weight: bold;
}
.home .controls .prev,
.home .controls .next {
    position: absolute;
    z-index: 2;
    top: 50%;
    height: 60px;
    width: 60px;
    margin-top: -30px;
    cursor: pointer;
    transition: all .5s ease;
    display: flex;
    justify-content: center;
    align-items: center;
}
.home .controls .prev {
    left: 0;
}
.home .controls .next {
    right: 0;
}
.prev span, .next span {
    border: solid rgb(60, 60, 60);
    border-width: 0 3px 3px 0;
    display: inline-block;
    padding: 3px;
    width: 20px;
    height: 20px;
}
.prev span {
    transform: rotate(135deg);
}
.next span {
    transform: rotate(-45deg);
}

.circles {
